.decoration {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  
} 

input {
  width: 300px;
  height: 60px;
  padding: 15px 15px;
  text-align: center;
  border-radius: 50px;
  border: none;
  background-color: rgb(0, 53, 160);
  margin-bottom: 20px;
  color: rgb(255, 255, 255);
  font-size: 25px;
}

button {
  width: 80px;
  height: 60px;
background-color: rgb(248, 248, 0);
  padding: 10px 0px;
  border-radius: 50px;
  font-size: 24px;
  color: rgb(252, 0, 0);
  margin-left: 20px;
  cursor: pointer;
}



.timer {
  width: 400px;
  display: flex;
  justify-content: space-between;
  color: rgb(3, 3, 3);
   font-style: italic;
   
}

.field {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 24px;
}
.label,
.value{
font-size: 27px;
}